import type { MenuProps } from './types'
import { Container, Flex, Img, List, ListItem, Link, Text, Center, Button } from '~/ui/primitives'
import logo from '~/assets/img/logo.svg'

interface Network {
  name: string
  href: string
}

interface MobileMenuProps extends MenuProps {
  networks: Network[]
}

const MobileMenu = ({ menuItems, networks }: MobileMenuProps) => (
  <Container
    position="fixed"
    display={{ xs: 'flex', lg: 'hidden' }}
    width="100vw"
    height="100vh"
    top="0"
    left="0"
    zIndex="z50"
    pointerEvents="none"
    overflow="hidden"
  >
    <Flex
      direction="col"
      justify="center"
      position="absolute"
      top="0"
      left="0"
      width="100%"
      height="100%"
      paddingY={32}
      opacity="0"
      pointerEvents="none"
      transform="translateX(100%)"
      visibility="invisible"
    >
      <Container variant="wrap">
        <Img src={logo} alt="Elena Bravo logo" size={{ xs: 120, sm: 150 }} pointerEvents="none" />
      </Container>
      <Container as="nav">
        <List>
          {menuItems.map((item) => (
            <ListItem key={item}>
              <Link href={`#${item}`}>
                <Text>{item}</Text>
              </Link>
            </ListItem>
          ))}
        </List>
      </Container>
    </Flex>
    <Container variant="wrap">
      <Container position="absolute" width="100%" bottom={40}>
        <List>
          <Center variant="center-self">
            {networks.map(({ name, href }) => (
              <Link href={href} key={name}>
                <Text>{name}</Text>
              </Link>
            ))}
          </Center>
        </List>
      </Container>
    </Container>
    <Button>
      <Text>Menu</Text>
    </Button>
    <Button>
      <Text>Close</Text>
    </Button>
  </Container>
)

export default MobileMenu
